import React, { useState } from "react";
import ClassNames from "classnames";
import { Player } from "@lottiefiles/react-lottie-player";
import { imagesWithMessage } from "./sample/mock-responce.";
import "./App.css";

const App: React.FC = () => {
  // アニメーションをシーンごとに分割
  const [section2, setSection2] = useState(false);
  const [section3, setSection3] = useState(false);
  const [section4, setSection4] = useState(false);
  const [section5, setSection5] = useState(true);
  const onClick = () => {
    setTimeout(() => {
      setSection2(true);
    }, 50);
    setTimeout(() => {
      setSection3(true);
    }, 100);
    setTimeout(() => {
      setSection4(!section4);
    }, 4500);

    // 最後 全体の透過を下げる
    setTimeout(() => {
      setSection5(!section5);
      // このあとモザイクアートへ
    }, 9000); // TODO:メッセージ量を見て採光したほうが良いかも
  };

  const mainClass = ClassNames("Main", {
    "Main--show": section5,
  });

  return (
    <div className={mainClass}>
      {!section2 && (
        <button type="button" className="Button" onClick={onClick}>
          Happy Wedding
        </button>
      )}

      {section3 && <div className="CircleBackGround" />}

      {section2 && (
        <Player
          src="https://assets3.lottiefiles.com/packages/lf20_5Vz7xX.json"
          background="transparent"
          speed={1}
          style={{ width: "80%", height: "80%" }}
          loop
          controls
          autoplay
        />
      )}

      {section4 && (
        <div className="MessageList">
          {/* 繰り返し要素 */}
          {imagesWithMessage.map((item, index) => (
            <div
              className="Column"
              key={index}
              style={{
                top: `calc(${Math.floor(Math.random() * 93)}%)`,
                left: `${Math.floor(Math.random() * 60)}%`, // TODO: 計算を調整したほうがいいと思われ
                animationDelay: `${index * 0.4}s`,
              }}
            >
              <div className="Column__item">
                <img
                  src={item.image}
                  width=""
                  height=""
                  alt="写真"
                  className="Thumbnail"
                />
                {/* <img src={item.image} width="" height="" alt="写真" /> */}
              </div>
              {item.comment && item.comment !== "" && (
                <div className="Column__item">
                  <p className="Comment">{item.comment}</p>
                </div>
              )}
            </div>
          ))}
          {/* 繰り返し要素 */}
        </div>
      )}
    </div>
  );
};

export default App;
